import request from '../../request'
//
export function lstPt(data) {
    return request({
        url: '/map/lstPt',
        method: 'post',
        data
    })
}
//
export function allDestination(data) { 
  return request({
      url: '/map/allDestination',
      method: 'post',
      data
  })
}
//
export function addlstPt(data) {
  return request({
    url: '/map/keypt',
    method: 'post',
    data
  })
}
//
export function editlstPt(data) {
  return request({
    url: '/map/updatePt',
    method: 'post',
    data,
    headers:{'content-type':'application/json'}
  })
}
//
export function dellstPt(data) {
  return request({
    url: '/map/deletePt',
    method: 'post',
    data
  })
}
//
export function checkArea(data) {
  return request({
    url: '/map/checkArea',
    method: 'post',
    data
  })
}

//
export function destinationByRoad(data) { 
  return request({
      url: '/map/destinationByRoad',
      method: 'post',
      data
  })
}
