<template>
  <router-view v-if="$route.path!=='/networkManagement'"></router-view>

  <div class="app-container" id="networkManagement" v-else>
    <div class="filter-container">
      <el-input v-model="keyword" placeholder="" class="filter-item" @keyup.enter.native="handleFilter" />
      <el-select v-model="networkVal" class="filter-item" placeholder="" @change="handleFilter">
        <el-option v-for="item in networkList" :key="item.type" :label="item.option" :value="item.type" />
      </el-select>
      <el-button class="filter-item" type="primary" icon="el-icon-search" @click="handleFilter"></el-button>
      <el-button class="filter-item" type="primary" @click="handleCreate" icon="el-icon-circle-plus-outline"></el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" icon="iconfont icondaochu" :loading="exportLoading"></el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="uploadVisible=true" icon="iconfont icondaoru"></el-button>

    </div>
    <div class="tableBox">

      <el-table v-loading="listLoading" :data="list" fit highlight-current-row :row-class-name="tableRowClassName">
        <el-table-column label="" width="60" align="center" prop="id" type="index" :index="indexMethod"></el-table-column>
        <!-- <el-table-column v-if="power==1" label="ID" align="center" prop="id"></el-table-column> -->
        <el-table-column label="" min-width="150" align="center" prop="name"></el-table-column>
        <el-table-column label="" min-width="150" align="center" prop="parentBankName"></el-table-column>
        <el-table-column label="" min-width="300" align="center" prop="address"></el-table-column>
        <el-table-column label="" width="130" align="center" prop="sendTime"></el-table-column>
        <el-table-column label="" width="130" align="center" prop="receiveTime"></el-table-column>
        <el-table-column label="" width="120" align="center" prop="networkType"></el-table-column>
        <el-table-column label="" width="80" align="center" prop="networkType">
          <template slot-scope="{row}">
            {{row.needToReport==2?'':''}}
          </template>
        </el-table-column>
        <el-table-column label="()" width="150" align="center" prop="radius"></el-table-column>
        <el-table-column label="" width="200" align="center" prop="createTime"></el-table-column>

        <el-table-column fixed="right" label="" align="center" min-width="230" class-name="small-padding fixed-width">
          <template slot-scope="{row}">
            <el-button type="primary" size="mini" @click="handleUpdate(row)" icon="el-icon-edit-outline"></el-button>
            <el-button type="danger" size="mini" @click="deleteConfirmOpen(row)" icon="el-icon-delete"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <pagination v-if="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />
    <my-upload :visible.sync="uploadVisible" :uploadData="{type:7}" @dataUpdated="reqSuccess"></my-upload>

  </div>
</template>

<script>
import Pagination from "@/components/Pagination"
import myUpload from "@/components/myUpload"; // secondary package based on el-pagination
// js
import { lstPt, dellstPt } from '../../../api/managementArchives/network'
import { formatDate } from "../../../../src/common";// 
import {
  exportExcelReq
} from "../../../api/managementArchives/common"; //,

export default {
  name: "ComplexTable",
  components: { Pagination, myUpload },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem('userInfo')).Power,// 
      list: [],// 
      total: 0,// 
      listLoading: false,// loading
      listQuery: {
        page: 1,
        limit: 10
      },
      indexMethod: 1,// 
      networkList: [// 
        { type: -1, option: '' },
        { type: 1, option: '' },
        { type: 2, option: 'ATM' },
        { type: 3, option: '' },
        { type: 4, option: '' },
        { type: 5, option: '' },
        { type: 6, option: '' },
        { type: 7, option: '' },
      ],
      networkVal: -1,// 
      exportLoading: false,
      uploadVisible: false,
      keyword:''
    };
  },
  watch: {
    //
    // $route(val) {
    //   let D = false;

    //   if (val.params.page) {// 
    //     D = val.params;
    //   };
    //   this.getData(D);

    // }
  },
  created() {
    if(this.$route.params.page&&this.$route.params.limit){
    this.listQuery.page=this.$route.params.page;
    this.listQuery.limit=this.$route.params.limit;
    }
    this.getList()
  },

  methods: {
     deleteConfirmOpen(row) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.deleteRoute(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    reqSuccess: function (val) {  //val
      if (val == true) {
        this.getList();
      }
    },
    getList() {// 
      let _t = this;
      _t.listLoading = true;
      lstPt({
        type: _t.networkVal,
        page: _t.listQuery.page,
        limit: _t.listQuery.limit,
        keyword:_t.keyword
      })
        .then(res => {
          if (res.status == 0) {
            _t.list = res.data.records;
            _t.list.forEach((item, index) => {
              _t.list[index].createTime = formatDate(item.createTime, 1);
              _t.list[index].networkType = _t.networkList[item.contentType].option;
            });
            _t.total = res.data.total;
            _t.indexMethod = (_t.listQuery.page - 1) * (_t.listQuery.limit) + 1;
          } else {
            _t.$message.error(res.msg);
          };
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },
    handleFilter(e) {// 
      // this.networkVal = e;
      this.listQuery.page = 1;
      this.getList();
    },
    // 
    getData(D) {
      let _t = this;
      if (D) {
        _t.total = 0;
        _t.listQuery.page = D.page;
        _t.listQuery.limit = D.limit;
      };
      _t.getList();
    },
    handleCreate() {// 
      // this.$router.push("/networkAdd");
      this.$router.push({ name: '', params: { page: this.listQuery.page, limit: this.listQuery.limit } });
    },
    handleUpdate(row) {// 
      // console.log(row)
      // 
      row.page = this.listQuery.page;
      row.limit = this.listQuery.limit;
      this.$router.push({ name: '', params: row });
    },
    deleteRoute(row) {// 
      let _t = this;
      _t.listLoading = true;
      dellstPt({ id: row.id }).then(res => {
        if (res.status == 0) {
          _t.$message.success('');
          if (_t.listQuery.page > 1 && _t.list.length <= 1) {// 
            _t.listQuery.page -= 1;
          };
          _t.getList();
        } else {
          _t.$message.error(res.msg);
          _t.listLoading = false;
        };
      }).catch(err => {
        _t.listLoading = false;
      });
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      this.exportLoading = true;
      // 
      exportExcelReq({ excelType: 9, title: '' }).then(res => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(res)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });

        } catch (error) {
          let disposition = res.headers;
          let fileName = "" + ".xls";
          let blob = new Blob([res], { type: "application/excel" }); // pdf
          let link = document.createElement("a");
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }
      }).finally(() => {
        this.exportLoading = false;
      });
    }
  }
};
</script>