<template>
  <div>
    <el-dialog title="" :visible.sync="openUploadDialog">
      <el-upload drag ref="upload" :limit="limitNum" :auto-upload="false" accept=".xlsx,.xls" :on-remove="handleRemove" :action="UploadUrl()" :before-upload="beforeUploadFile" :on-change="fileChange" :on-exceed="exceedFile" :on-success="handleSuccess" :on-error="handleError" :file-list="fileList">
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">xlsxxls,10M</div>
        <div class="el-upload__tip red" slot="tip"></div>
      </el-upload>
      <br />
      <div slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" @click="uploadFile"></el-button>
        <el-button size="small" @click="openUploadDialog=false"></el-button>

      </div>

    </el-dialog>
  </div>
</template>
<script>
import {

  importExcelReq,
  uploadReq
} from "../../api/managementArchives/common";
export default {
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    // uploadReq: {
    //   type: Object,
    //   required: true,
    // },
    limitNum: {
      type: Number,
      default: 1
    },
    uploadData: {
      type: Object,
      required: true,
    },
    dataUpdated: {
      type: Boolean,
      default: false
    }

  },
  data() {
    return {
      fileList: [],
    }
  },
  computed: {
    openUploadDialog: {
      get() {
        return this.visible
      },
      set(val) {
        this.$emit('update:visible', val) // openUploadDialog
         this.$refs.upload.clearFiles();
        this.fileList=[]
      }
    }
  },

  methods: {
    handleRemove(file, fileList) {
      if (file && file.status === "success") {
        //
        this.fileList = []
      }
    },
    // 
    exceedFile(files, fileList) {
      this.$message.warning(` ${this.limitNum}  ${files.length + fileList.length} `);
    },
    // 
    fileChange(file, fileList) {
      console.log(file.raw);
      this.fileList = [file.raw]
      console.log(this.fileList);
    },
    // , , false  Promise  reject
    beforeUploadFile(file) {

      let extension = file.name.substring(file.name.lastIndexOf('.') + 1);
      let size = file.size / 1024 / 1024;
      if (extension !== 'xlsx' && extension !== 'xls' && extension !== 'XLSX' && extension !== 'XLS') {
        this.$message.warning('.xlsx.xls');
        return false
      }
      if (size > 10) {
        this.$message.warning('10M');
        return false
      }
    },
    // 
    handleSuccess(res, file, fileList) {
      this.$message.success('');
    },
    // 
    handleError(err, file, fileList) {
      this.$message.error('');
    },
    UploadUrl: function () {
      // actionurlapi404action
      return ""
    },
    uploadFile() {
      this.$emit('dataUpdated', false);
      if (this.fileList.length === 0) {
        this.$message.warning('');
      } else {
        let form1 = new FormData();


        uploadReq({ file: this.fileList[0] })
          .then(res => {
            this.uploadData.fileName = res.data.filename;
            this.uploadData.path = res.data.path;
            importExcelReq(this.uploadData)
              .then(res1 => {
                this.$message.success(res1.data);
                this.$emit('dataUpdated', true);
                this.openUploadDialog = false;

              })
              .catch(() => {

              }).finally(() => {
                this.$refs.upload.clearFiles();
                ths.fileList = []

              });
          })
          .catch(() => {
          
          });
      }
    }

  }
}
</script>
<style scoped lang="scss">
.el-upload__tip {
  font-size: 14px;
  &.red {
    color: red;
    font-size: 16px;
  }
}
</style>