<template>
  <div id="networkAdd">
    <div class="left">
      <el-form v-loading="myLoading" class="routeForm" ref="temp" :model="temp" :rules="rules" label-position="left" style="margin-left:0;">
        <el-form-item prop="networkVal">
          <el-select v-model="temp.networkVal" @change="changeType" class="filter-item" placeholder="">
            <el-option v-for="item in networkList" :key="item.type" :label="item.option" :value="item.type" />
          </el-select>
        </el-form-item>
        <el-form-item prop="bank">
          <el-select filterable v-model="temp.bank" class="filter-item" placeholder="">
            <el-option v-for="item in bankOptions" :key="item.id" :label="item.name" :value="item.id" :disabled="item.status==2" />
          </el-select>
        </el-form-item>
        <el-form-item prop="roadName">
          <el-input placeholder="" v-model="temp.roadName" />
        </el-form-item>
        <el-form-item prop="sendTime">
          <el-time-picker v-model="temp.sendTime" value-format="HH:mm:ss" :picker-options="{selectableRange: '00:00:00 - 23:59:59'}" placeholder=""></el-time-picker>
        </el-form-item>
        <el-form-item prop="receiveTime">
          <el-time-picker v-model="temp.receiveTime" value-format="HH:mm:ss" :picker-options="{selectableRange: '00:00:00 - 23:59:59'}" placeholder=""></el-time-picker>

        </el-form-item>
        <el-form-item prop="networkSearchVal">
          <el-input id="suggestId" v-model="temp.networkSearchVal" placeholder="" />
          <div id="r-result"></div>
        </el-form-item>

        <el-form-item v-if="!clockIn" prop="finalMarkePoints" class="lnglat">
          <el-input placeholder="" v-model="temp.finalMarkePoints" />
        </el-form-item>

        <el-form-item v-if="clockIn" prop="pts" class="lnglat">
          <el-input placeholder="" v-model="temp.pts" />
        </el-form-item>
        <el-form-item prop="needToReport">
          <el-select v-model="temp.needToReport" class="filter-item" placeholder="">
            <el-option v-for="item in needToReportOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item v-if="!clockIn" prop="radius" :label="` ${myRadius>1000?`${myRadius/1000} `:`${myRadius} `}`" class="radius">
          <el-slider v-model="temp.radius" show-input input-size="mini" :max="radiusMax" :format-tooltip="formatTooltip" @input="changeRadius"></el-slider>
        </el-form-item>

        <el-form-item>
          <el-button v-show="!clockIn" type="primary" class="addNetwork" @click="confirmAddNetwork('temp')"></el-button>
          <el-button v-show="clockIn" type="primary" class="addNetwork" @click="clockSubmit('temp')"></el-button>
          <el-button type="primary" class="addNetwork" @click="goBack"></el-button>
        </el-form-item>

        <p class="tip"></p>
      </el-form>
    </div>
    <div class="content">
      <div id="historyRouteMap" ref="historyRouteMap"></div>
    </div>
    <div class="opr">
      <el-button v-show="!clockIn" type="primary" size="small" round class="oprbtn" @click="setPoint"></el-button>
      <el-button v-show="clockIn" type="primary" size="small" round class="oprbtn" @click="setArea"></el-button>
      <el-button type="primary" size="small" round class="oprbtn" @click="clearAll"></el-button>
    </div>
  </div>
</template>
<script>
// js
import {
  addlstPt,
  editlstPt,
  checkArea
} from "../../../api/managementArchives/network";
import { selectBank } from '../../../api/managementArchives/bank';

export default {
  data() {
    var addressV = (rule, value, callback) => {
      // 
      // 
      if (this.temp.networkSearchVal != "") {
        if (value == "") {
          callback(new Error(""));
        } else {
          callback();
        }
      } else {
        callback();
      }
    };
    var areaV = (rule, value, callback) => {
      // 
      // 
      if (this.temp.networkSearchVal != "") {
        if (value == "") {
          callback(new Error(""));
        } else {
          callback();
        }
      } else {
        callback();
      }
    };
    return {
      id: undefined,
      map: undefined,
      myCircle: undefined, // 
      radiusMax: 10000,
      prevData: {}, // 
      myLoading: false, // loading
      searchMarke: [],
      clockIn: false, // 
      myPolygon: undefined, // 
      myM: undefined, // 
      myDrawingManager: undefined, // 
      myDobj: {
        isOpen: false, //
        enableDrawingTool: false, //
        drawingToolOptions: {
          anchor: BMAP_ANCHOR_TOP_RIGHT, //
          offset: new BMap.Size(5, 5), //
          drawingModes: [BMAP_DRAWING_MARKER, BMAP_DRAWING_POLYGON]
        },
        polygonOptions: {
          strokeColor: "red", //
          fillColor: "#e28080", //
          strokeWeight: 3, //
          strokeOpacity: 0.8, //0 - 1
          fillOpacity: 0.6, //0 - 1
          strokeStyle: "solid" //soliddashed
        }
      }, //  
      networkList: [
        // 
        { type: 1, option: "" },
        { type: 2, option: "ATM" },
        // { type: 3, option: "" },
        { type: 4, option: "" },
        // { type: 5, option: "" },
        { type: 6, option: "" },
        // { type: 7, option: "" }
      ],
      networkAddLocal: {},
      userlocation: {}, // 
      temp: {
        roadName: "", // 
        networkSearchVal: "", // 
        networkVal: "", // 
        finalMarkePoints: "", // 
        radius: 50, // 
        pts: "", // 
        bank: "",
        sendTime: "",
        receiveTime: "",
        needToReport: 2,

      },
      rules: {
        roadName: [
          { required: true, message: "", trigger: "blur" }
        ],
        networkSearchVal: [
          { required: true, message: "", trigger: "blur" }
        ],
        finalMarkePoints: [{ validator: addressV, trigger: "blur" }],
        networkVal: [
          { required: true, message: "", trigger: "change" }
        ],
        bank: [
          { required: true, message: "", trigger: "change" }
        ],
        pts: [{ validator: areaV, trigger: "blur" }],
        needToReport: [
          { required: true, message: "", trigger: "change" }
        ],
      },
      bankOptions: [],
      needToReportOptions: [
        { id: 1, name: '' },
        { id: 2, name: '' }

      ],
    };
  },
  computed: {
    myRadius: {
      get() {
        return this.temp.radius * 1;
      },
      set(val) {
        this.temp.radius = val / 1;
      }
    }
  },
  mounted() {
    this.initMap();
  },
  methods: {
    //
    initMap() {
      this.createMap(); //
      this.setMapEvent(); //
      this.addMapControl(); //
      this.getSelect();
    },
    // 
    createMap() {
      let _t = this;
      _t.map = new BMap.Map("historyRouteMap", { enableMapClick: false }); //
      // var point = new BMap.Point(113.925973657, 22.5760535462); //
      // _t.map.centerAndZoom(point,13); //
      _t.map.enableScrollWheelZoom();
      _t.getPrevData(); // 
      if (_t.id == undefined) {
        _t.map.centerAndZoom("", 13);
        // var myCity = new BMap.LocalCity();
        // myCity.get(function(res){
        //   var cityName = res.name;
        //   _t.map.setCenter(cityName);
        // });
      }

      // 
      // _t.map.addEventListener('click',function(e){
      //   _t.drawMarker(e.point);// 
      // });

      // 
      var ac = new BMap.Autocomplete({
        // api
        //
        input: "suggestId",
        location: _t.map,
        onSearchComplete: function (e) {
          // 
          _t.clearAll();
        }
      });
      ac.setInputValue(_t.temp.networkSearchVal); // 
      var myValue;
      ac.addEventListener("onconfirm", function (e) {
        // console.log(e);
        //
        var _value = e.item.value;
        myValue =
          _value.province +
          _value.city +
          _value.district +
          _value.street +
          _value.business;
        _t.temp.networkSearchVal = myValue;
        _t.setPlace();
      });
    },

    //
    setMapEvent() {
      this.map.enableDragging(); //()
      this.map.enableScrollWheelZoom(); //
      this.map.enableDoubleClickZoom(); //()
      this.map.enableKeyboard(); //
    },

    //
    addMapControl() {
      //
      var ctrl_nav = new BMap.NavigationControl({
        anchor: BMAP_ANCHOR_TOP_LEFT,
        type: BMAP_NAVIGATION_CONTROL_LARGE
      });
      // this.map.addControl(ctrl_nav);
      //
      var ctrl_sca = new BMap.ScaleControl({ anchor: BMAP_ANCHOR_BOTTOM_LEFT });
      var overView = new BMap.OverviewMapControl();

      var mapType1 = new BMap.MapTypeControl({
        mapTypes: [BMAP_NORMAL_MAP, BMAP_HYBRID_MAP],
        anchor: BMAP_ANCHOR_TOP_LEFT
      });
      // this.map.addControl(ctrl_sca);

      this.map.addControl(mapType1); //2D
    },

    //
    delete_control() {
      this.map.removeControl(mapType1); //2D
      this.map.removeControl(overView);
      this.map.removeControl(overViewOpen);
    },
    // 
    confirmAddNetwork(temp) {
      let _t = this;
      // id
      this.$refs[temp].validate((valid, obj) => {
        if (valid) {
          _t.myLoading = true;

          if (_t.id) {
            // 
            let myData = {
              id: _t.id,
              name: _t.temp.roadName,
              address: _t.temp.networkSearchVal,
              contentType: _t.temp.networkVal,
              geom: `POINT(${_t.temp.finalMarkePoints.split(",")[0]} ${_t.temp.finalMarkePoints.split(",")[1]
                })`,
              radius: _t.myRadius,
              geomtype: _t.prevData.geomtype,

              status: _t.temp.status,
              needToReport: _t.temp.needToReport
            };
            if (_t.temp.sendTime) {
              myData.sendTime = _t.temp.sendTime;
            }
            if (_t.temp.receiveTime) {
              myData.receiveTime = _t.temp.receiveTime;
            }
            // if (_t.temp.networkVal == 1 || _t.temp.networkVal == 2) {
            let obj = {};
            obj = this.bankOptions.find((item) => {
              return item.id === _t.temp.bank;
            });
            let bankName = ''
            bankName = obj.name;
            myData.parentBankId = _t.temp.bank;
            myData.parentBankName = bankName
            // }
            editlstPt(myData)
              .then(res => {
                if (res.success) {
                  _t.$message.success("");
                } else {
                  _t.$message.error(res.msg);
                }
              })
              .finally(() => {
                _t.myLoading = false;
              });
          } else {

            // 
            let myData = {
              name: _t.temp.roadName,
              address: _t.temp.networkSearchVal,
              type: _t.temp.networkVal,
              lng: _t.temp.finalMarkePoints.split(",")[0],
              lat: _t.temp.finalMarkePoints.split(",")[1],
              radius: _t.myRadius,
              needToReport: _t.temp.needToReport


            };
            if (_t.temp.sendTime) {
              myData.sendTime = _t.temp.sendTime;
            }
            if (_t.temp.receiveTime) {
              myData.receiveTime = _t.temp.receiveTime;
            }

            // if (_t.temp.networkVal == 1 || _t.temp.networkVal == 2) {
            let obj = {};
            obj = this.bankOptions.find((item) => {
              return item.id === _t.temp.bank;
            });
            let bankName = ''
            bankName = obj.name;
            myData.parentBankId = _t.temp.bank;
            myData.parentBankName = bankName
            // }
            addlstPt(myData)
              .then(res => {
                if (res.success) {
                  _t.$message.success("");
                  // _t.$refs[temp].resetFields();
                  _t.resetForm();
                  _t.map.clearOverlays();
                } else {
                  _t.$message.error(res.msg);
                }
              })
              .finally(() => {
                _t.myLoading = false;
              });
          }
        } else {

          return false;
        }
      });
    },
    //
    clockSubmit(temp) {
      let t = this;
      t.$refs[temp].validate((valid, obj) => {
        if (valid) {
          t.myLoading = true;
          let newPts = t.temp.pts.split(",");
          newPts.push(newPts[0]);
          newPts = newPts.toString();
          if (t.id) {
            // 
            let myData = {
              id: t.id,
              name: t.temp.roadName,
              address: t.temp.networkSearchVal,
              // contentType: t.temp.networkVal,
              geom: `POLYGON((${newPts}))`,
              // radius: t.myRadius,
              // geomtype: t.prevData.geomtype,

              // status:_t.temp.status

            };
            //  if(t.temp.sendTime){
            // myData.sendTime=t.temp.sendTime;
            // }
            // if(t.temp.receiveTime){
            // myData.receiveTime=t.temp.receiveTime;
            // }
            // let obj = {};
            //   obj = this.bankOptions.find((item) => {
            //     return item.id === _t.temp.bank;
            //   });
            //   let bankName = ''
            //   bankName = obj.name;
            //   myData.parentBankId = _t.temp.bank;
            //   myData.parentBankName = bankName
            editlstPt(myData)
              .then(res => {
                if (res.success) {
                  t.$message.success("");
                } else {
                  t.$message.error(res.msg);
                }
              })
              .finally(() => {
                t.myLoading = false;
              });
          } else {
            // 
            let myData = {
              name: t.temp.roadName,
              address: t.temp.networkSearchVal,
              pts: newPts

            };
            //  if(t.temp.sendTime){
            // myData.sendTime=t.temp.sendTime;
            // }
            // if(t.temp.receiveTime){
            // myData.receiveTime=t.temp.receiveTime;
            // }
            checkArea(myData)
              .then(res => {
                if (res.success) {
                  t.$message.success("");
                  t.resetForm();
                  t.map.clearOverlays();
                } else {
                  t.$message.error(res.msg);
                }
              })
              .finally(() => {
                t.myLoading = false;
              });
          }
        } else {
          return false;
        }
      });
    },
    resetForm() {
      // 
      let t = this;
      t.temp.roadName = ""; // 
      t.temp.networkSearchVal = ""; // 
      t.temp.finalMarkePoints = ""; // 
      t.temp.pts = ""; // 
    },
    setPlace() {
      // 
      var that = this;
      this.map.clearOverlays(); //

      var local = new BMap.LocalSearch(this.map, {
        //
        onSearchComplete: this.myFun
      });
      that.networkAddLocal = local;
      local.search(that.temp.networkSearchVal);
    },
    myFun() {
      var _t = this;
      _t.userlocation = _t.networkAddLocal.getResults().getPoi(0).point; //
      _t.drawMarker(_t.userlocation); // 
    },
    // 
    goBack() {
         this.$router.push({name:'',params:{page:this.$route.params.page,limit:this.$route.params.limit}});
    },
    // 
    getPrevData() {
      let _t = this;
      _t.prevData = this.$route.params; // 
      // console.log('')
      // console.log(_t.prevData)
      if (_t.prevData.id) {
        // id
        _t.id = _t.prevData.id;
        _t.temp.roadName = _t.prevData.name;
        _t.temp.networkSearchVal = _t.prevData.address;
        _t.temp.networkVal = _t.prevData.contentType;
        _t.temp.bank = _t.prevData.parentBankId;
        _t.temp.status = _t.prevData.status;
        _t.temp.needToReport = _t.prevData.needToReport;
        if (_t.prevData.sendTime) {
          _t.temp.sendTime = _t.prevData.sendTime
        }
        if (_t.prevData.receiveTime) {
          _t.temp.receiveTime = _t.prevData.receiveTime
        }

        // geomlnglat
        if (_t.prevData.contentType == 7) {
          // 
          _t.clockIn = true;
          let geoms = _t.prevData.geom.slice(9, -2);
          _t.drawPolygon(geoms);
        } else {
          // let regex = /POINT\((.*?)\)/ig;
          let geom = _t.prevData.geom.slice(6, -1).split(" ");
          // 
          _t.myRadius = _t.prevData.radius;
          // _t.temp.finalMarkePoints = geom[0]+','+geom[1];
          _t.drawMarker(new BMap.Point(geom[0], geom[1])); // drawMarker()
        }
      } else {
        // id
        // console.log('add')
      }
    },
    // 
    drawMarker(P) {
      // Ppoint

      let _t = this,
        myMartker = new BMap.Marker(P, { enableDragging: true });
      _t.myCircle = new BMap.Circle(P, _t.myRadius, {
        strokeColor: "blue",
        strokeWeight: 2,
        strokeOpacity: 0.5
      });
      // 
      _t.temp.finalMarkePoints = `${P.lng},${P.lat}`;
      _t.$refs.temp.validateField("finalMarkePoints"); // 
      // 
      _t.map.clearOverlays();
      if (!_t.clockIn) {
        // 
        _t.map.addOverlay(_t.myCircle); //
        // 
        let myBounds = _t.myCircle.getBounds();
        let sw = myBounds.getSouthWest(),
          ne = myBounds.getNorthEast();
        let result = [
          sw,
          ne,
          new BMap.Point(sw.lng, ne.lat),
          new BMap.Point(ne.lng, sw.lat)
        ];
        _t.map.setViewport(result, { margins: [100, 100, 100, 100] });
      } else {
        let myZoom = _t.map.getZoom();
        _t.map.centerAndZoom(P, myZoom > 4 ? myZoom : 17);
      }

      _t.map.addOverlay(myMartker); //
      _t.myM = myMartker;
      // 
      myMartker.addEventListener("dragend", function (e) {
        // console.warn(e);
        _t.temp.finalMarkePoints = `${e.point.lng},${e.point.lat}`;
      });
      myMartker.addEventListener("dragging", function (e) {
        // console.warn(e);
        _t.myCircle.setCenter(e.point);
      });
    },
    // 
    formatTooltip(val) {
      let value = this.myRadius;
      if (value > 1000) {
        return value / 1000 + "";
      } else {
        return value + " ";
      }
    },
    changeRadius() {
      // 
      this.myCircle && this.myCircle.setRadius(this.myRadius);
    },
    setPoint() {
      // 
      let t = this,
        myFun = function (x) {
          t.drawMarker(x.point); // 
          t.map.removeEventListener("click", myFun);
        };
      t.map.addEventListener("click", myFun);
    },
    setArea() {
      // 
      let t = this;
      t.myPolygon && this.map.removeOverlay(t.myPolygon); // 
      t.myDrawingManager = new BMapLib.DrawingManager(t.map, t.myDobj); // 
      t.myDrawingManager.open();
      t.myDrawingManager.setDrawingMode(BMAP_DRAWING_POLYGON);
      t.myDrawingManager.addEventListener("polygoncomplete", function (
        e,
        overlay
      ) {
        t.temp.pts = ""; // 
        t.myPolygon = overlay;
        overlay.getPath().forEach(x => {
          // 
          t.temp.pts += `${x.lng} ${x.lat},`;
        });
        t.temp.pts = t.temp.pts.slice(0, -1);
      });
    },
    clearAll() {
      // 
      this.map.clearOverlays();
      this.temp.finalMarkePoints = ``;
      this.temp.pts = "";
      this.myM = undefined;
    },
    changeType(e) {
      // (type:7)
      let t = this;
      if (e == 7) {
        // 
        t.clockIn = true;
        t.myCircle && t.map.removeOverlay(t.myCircle); // 
      } else {
        t.clockIn = false;
        t.myPolygon && this.map.removeOverlay(t.myPolygon); // 
        t.temp.pts = ""; // 
        t.myM && t.myCircle && t.map.addOverlay(t.myCircle); // 
      }
    },
    drawPolygon(list) {
      // 
      let t = this,
        map = t.map,
        paths = list.split(","),
        result = [];
      paths.pop();
      t.temp.pts = paths.toString();
      paths.forEach(x => {
        let res = x.split(" ");
        result.push(new BMap.Point(res[0], res[1]));
      });
      t.map.clearOverlays();
      t.map.setViewport(result, { margins: [100, 100, 100, 100] });
      // 
      t.myPolygon = new BMap.Polygon(result, {
        strokeColor: "red", //
        fillColor: "#e28080", //
        strokeWeight: 3, //
        strokeOpacity: 0.8, //0 - 1
        fillOpacity: 0.6, //0 - 1
        strokeStyle: "solid" //soliddashed
      });
      map.addOverlay(t.myPolygon);
    },
    //
    getSelect() {
      let that = this;

      selectBank().then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            // that.networkOptions = [{ id: -1, name: "" }];
            return;

          }

          that.bankOptions = res.data;

          // that.networkOptions.unshift({ id: -1, name: "" });
        } else {
          if (res.data == null) {
            this.bankOptions = [];
          }
          this.$message.error(res.msg);
        }
      });



    },
  }
};
</script>
