</style>
<style lang="scss">
$formItem-height: 69px;
$formItem-borderColor: #bfbfbf;
$theme-color: #324890;
$headerBarH: 120px;
$leftBarW: 240px;
#networkAdd {
  .left {
    .el-input__icon {
      line-height: 26px;
    }
    .el-input__inner,
    .el-textarea__inner {
