import request from '../../request'
//
export function teamsSelect(data) {
    return request({
        url: 'teams/select',
        method: 'post',
        data
    })
}
//
export function groupsSelect(data) {
    return request({
        url: 'groups/select',
        method: 'post',
        data
    })
}

//
export function personsSelect(data) {
    return request({
        url: 'persons/select',
        method: 'post',
        data
    })
}
//
export function selectByCondition(data) {
    return request({
        url: 'persons/selectByCondition',
        method: 'post',
        data,
        headers:{'content-type':'application/json'},

    })
}
